

@font-face {
	font-family: 'icons';
	font-display: swap;
	 src:url("./fonts/icons.woff") format("woff");

	font-weight: normal;
	font-style: normal;
	font-display: block;
}
%ic {
	font-family: 'icons';
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none; 
	list-style: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

[class^="_icon-"],
[class*=" _icon-"] {
	&:before {
		@extend %ic;
		display: block;
	}
}

